import Layout from '../pages/layout/Layout'
import { Index } from '../pages/index/Index'
import {
    MonitoringList,
    MonitoringDetails
} from '../pages/monitoring'

import {
    AccountList,
    AccountHandle
} from '../pages/account'
import {
    CompanyList
} from '../pages/company'
import {
    TaskList
} from '../pages/taskInfo'
import {
    FileList
} from '../pages/other'
import {
    ImgList
} from '../pages/pictures'
import {
    MessageList
} from '../pages/message'
import {
    ClassifyList
} from '../pages/classify'

/**
 * name: 
 * redirect : noredirect 
 **/
export default [{
    path: '/',
    component: Layout,
    children: [{
        path: 'index',
        component: Index,
        name: "",
    }, {
        path: 'account',
        component: AccountList,
        name: ''
    }, {
        path: 'account/add',
        component: AccountHandle,
        name: ''
    }, {
        path: 'account/edit',
        component: AccountHandle,
        name: ''
    }, {
        path: 'company',
        component: CompanyList,
        name: ''
    }, {
        path: 'tasks',
        component: TaskList,
        name: ''
    }, {
        path: 'other',
        component: FileList,
        name: ''
    }, {
        path: 'pictures',
        component: ImgList,
        name: ''
    }, {
        path: 'message',
        component: MessageList,
        name: ''
    }]
}, {
    path: '/monitoring',
    component: Layout,
    children: [{
            path: 'have',
            component: MonitoringList,
            name: '',
        },
        {
            path: 'not',
            component: MonitoringList,
            name: ''
        }, {
            path: 'details',
            component: MonitoringDetails,
            name: ''
        }
    ]
}, {
    path: '/classify',
    component: Layout,
    children: [{
            path: 'big',
            component: ClassifyList,
            name: '',
        },
        {
            path: 'small',
            component: ClassifyList,
            name: ''
        }
    ]
}
]


