<template>
  <div>
    <div class="margin-bottom-20 text-right">
      <router-link to="account/add">
        <el-button type="primary" size="small"></el-button>
      </router-link>
    </div>
    <el-table v-loading="loading" border :data="listData" tooltip-effect="dark">
      <el-table-column prop="company" label=""></el-table-column>
      <el-table-column prop="department" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="user_name" label=""></el-table-column>
      <el-table-column prop="job" label=""></el-table-column>
      <el-table-column label="" width="150px">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="editFn(scope.row.user_id)"
            title=""></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="deleteFn(scope.row)"
            title=""></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination @size-change="sizeChange" @current-change="currentChange" :current-page="page.current"
      :page-size="page.size" :total="page.total" :page-sizes="[10, 20, 50, 100, 200]"
      layout="total, sizes, prev, pager, next, jumper"></el-pagination>
  
  </div>
</template>

<script>
import * as Http from "@/api/home";
export default {
  inject: ["reload"],
  data() {
    return {
      loading: false,
      listData: [],
      page: {
        current: 1,
        size: 10,
        total: 0
      },
      dialog: {
        show: false,
        type: ""
      }
    };
  },
  created() {
    this.getListData();
  },
  methods: {
    getListData() {
      // 
      this.loading = true;
      let params = {
        page: this.page.current
      };
      Http.getAccountList(params)
        .then(res => {
          this.loading = false;
          this.$handleResponse(res, res => {
            this.listData = res.data;
            this.page.total = res.total;
          });
        })
        .catch(err => {
          this.loading = false;
        });
    },
    onSearch() {
      // 
      this.page.current = 1;
      this.getListData();
    },
    onReset() {
      // 
      this.menuCon.keyword = "";
      this.getListData();
    },
    editFn(user_id) {
      // 
      this.$router.push({
        path: "account/edit",
        query: {
          userId: user_id
        }
      });
    },
    deleteFn(data) {
      // 
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.doDelete(data.user_id);
        })
        .catch(() => {});
    },
    doDelete(id) {
      this.loading = true;
      let params = {
        user_id: id
      };
      Http.delAccount(params)
        .then(res => {
          this.$handleResponse(res, res => {
            this.$message.success("");
            this.reload();
          });
        })
        .catch(error => {
          this.loading = false;
          this.$message.error("");
        });
    },
    sizeChange(val) {
      this.page.size = val;
      this.getListData();
    },
    currentChange(val) {
      this.page.current = val;
      this.getListData();
    },
    dialogSuccess() {
      // 
      this.dialog.show = false;
      this.getListData();
    }
  }
};
</script>




