<template>
  <div>
    <div class="margin-bottom-20 text-right">
      <el-button type="primary" size="small" @click="dialogShow('add', {})"></el-button>
    </div>
    <el-table v-loading="loading" border :data="listData" tooltip-effect="dark">
      <el-table-column prop="company_name" label="" width="150px"></el-table-column>
      <el-table-column prop="position_detail_sname" label="" width="120px"></el-table-column>
      <el-table-column prop="position_detail_name" label="" width="120px"></el-table-column>
      <el-table-column prop="task_name" label="" width="140px"></el-table-column>
      <el-table-column prop="task_risk_init_level" label="" width="110px"></el-table-column>
      <el-table-column prop="task_status_text" label=""></el-table-column>
      <el-table-column prop="task_risk_level_text" label=""></el-table-column>
      <el-table-column prop="task_desc.risk_for" label="" width="200px"></el-table-column>
      <el-table-column prop="task_desc.risk_desc" label="" width="150px"></el-table-column>
      <el-table-column prop="task_desc.risk_to_do" label="" width="100px"></el-table-column>
      <el-table-column prop="task_desc.risk_type" label=""></el-table-column>
      <el-table-column prop="task_desc.risk_result" label="" width="150px"></el-table-column>
      <el-table-column prop="task_desc.risk_evaluate_technology" label="" width="180px"></el-table-column>
      <el-table-column prop="task_desc.risk_evaluate_to_do" label="" width="150px"></el-table-column>
      <el-table-column prop="task_desc.risk_evaluate_train" label="" width="180px"></el-table-column>
      <el-table-column prop="task_desc.risk_evaluate_protect" label=""></el-table-column>
      <el-table-column prop="task_desc.risk_evaluate_emergency" label=""></el-table-column>
      <el-table-column prop="task_desc.risk_level" label="" width="100px"></el-table-column>
      <el-table-column prop="task_desc.row" label="" width="180px"></el-table-column>
      <el-table-column prop="create_time_text" label="" width="150px"></el-table-column>
      <el-table-column label="" width="210px">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="dialogShow('edit', scope.row)"
            title=""></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="deleteFn(scope.row)"
            title=""></el-button>
          <el-button size="mini" type="primary" @click="dialogQCode(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>


    <!--  -->
    <el-pagination @size-change="sizeChange" @current-change="currentChange" :current-page="page.current"
      :page-size="page.size" :total="page.total" :page-sizes="[10, 20, 50, 100, 200]"
      layout="total, sizes, prev, pager, next, jumper"></el-pagination>
    
    <!--  -->
    <task-handle
      v-if="dialog.show"
      :type="dialog.type"
      :taskParent="dialog.taskParent"
      @cancel="dialog.show = !dialog.show"
      @success="dialogSuccess">
    </task-handle>

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="400px">
        <div class="text-center">
          <vue-qr :text="qrVal"></vue-qr>
        </div>
    </el-dialog>

  </div>
</template>

<script>
import * as Http from "@/api/home";
import TaskHandle from './TaskHandle';
import VueQr from 'vue-qr'

export default {
  components: {
    TaskHandle,
    VueQr
  },
  inject: ["reload"],
  data() {
    return {
      dialogVisible: false,
      loading: false,
      listData: [],
      page: {
        current: 1,
        size: 10,
        total: 0
      },
      dialog: {
        show: false,
        type: "",
        taskParent: {}
      },
      qrVal: ''
    };
  },
  created() {
    this.getListData();
  },
  methods: {
    getListData() {
      this.loading = true;
      let params = {
        page: this.page.current
      };
      Http.getTaskDesc(params)
        .then(res => {
          this.loading = false;
          this.$handleResponse(res, res => {
            this.listData = res.data;
            this.page.total = res.total;
          });
        })
        .catch(err => {
          this.loading = false;
        });
    },
    onSearch() {
      // 
      this.page.current = 1;
      this.getListData();
    },
    onReset() {
      // 
      this.menuCon.keyword = "";
      this.getListData();
    },
    // 
    dialogShow(type, initData){ 
      this.dialog.type = type;
      this.dialog.taskParent = initData;
      this.dialog.show = true;
    },
    deleteFn(data) {
      // 
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.doDelete(data.task_id);
        })
        .catch(() => {});
    },
    doDelete(id) {
      this.loading = true;
      let params = {
        task_id: id
      };
      Http.delTaskDesc(params)
        .then(res => {
          this.$handleResponse(res, res => {
            this.$message.success("");
            this.reload();
          });
        })
        .catch(error => {
          this.loading = false;
          this.$message.error("");
        });
    },
    sizeChange(val) {
      this.page.size = val;
      this.getListData();
    },
    currentChange(val) {
      this.page.current = val;
      this.getListData();
    },
    dialogSuccess() {
      // 
      this.dialog.show = false;
      this.getListData();
    },
    dialogQCode(val) {
      this.qrVal = String(val.task_desc_id);
      this.dialogVisible = true;
    }
  }
};
</script>




